.NavbarItems {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.2rem;
  padding: 0 30px;
  background-color: transparent;
   background-color:white; 
   /* box-shadow: 0 5px 15px rgba(0, 0, 0, 0.25);  */
  width: 100%;
  height: 80px;
  /* border-radius: 13px; */
  position: fixed;
  top: 0px;
  left: 50%;
  transform: translate(-50%);
}
.logo {
  
  justify-self: start;
  cursor: pointer;
}
.nav-menu {
  display: grid;
  grid-template-columns: repeat(6, auto);
  grid-gap: 10px;
  list-style: none;
  align-items: center;
  text-align: center;
  justify-content: end;
}
.nav-links {
  text-decoration: none;
  color:black;
  padding: 0.4rem 1rem;
  white-space: nowrap;
}
.nav-links:hover {
  background:rgb(255, 106, 0);
   color: white;
  background-blend-mode: lighten;
  border-radius: 4px;
  transition: all 0.2s ease-in-out;

  /* text-decoration: underline;
  text-decoration-color:orange ;
  text-decoration-thickness: 2px;
   */
}

.fa-bars,
.fa-times {
  color: black;
}

.menu-icons {
  display: none;
}

@media screen and (max-width: 850px) {
  .NavbarItems {
    z-index: 99;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background: rgba(255, 255, 255, 0.25);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(4px);
    border-radius: 13px;
    width: 100%;
    height: auto;
    position: absolute;
    top: 0;
    left: -110%;
    align-items: stretch;
    padding: 80px 0 30px 0;
    margin: 0;
    z-index: -1;
    transition: all 0.3s ease-in-out;
  }

  .nav-menu.active {
    left: 0%;
  }

  .nav-links {
    display: block;
    widows: 100%;
    padding: 2rem 0;
    color: #222;
  }
  .btns{
    display: block;
    widows: 100%;
    padding: 2rem 0;
  }
  .nav-links:hover {
    background: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(20px);
  }
  .btns:hover {
    background: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(20px);
  }
  .menu-icons {
    display: block;
  }
}
